import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Evento_campanha } from '../../../modelo/Evento_campanha'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'evento_campanha-dados-details',   templateUrl: 'evento_campanha_dadosDetails.component.html' }) export class Evento_campanhaDadosDetailsComponent implements OnInit {    evento_campanha: Evento_campanha;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.evento_campanha.Evento_campanha";                 let id = +params['id'];         this.evento_campanha = new Evento_campanha();         this.evento_campanha.cd_evento_campanha = id;         o.objetoJson = this.evento_campanha;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a evento_campanha. '; }      this.evento_campanha = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


